.containerform {
  width: var(--containerWidth);
  background: white;
  text-align: center;
  border-radius: 5px;
  padding: 50px 35px 10px 35px;
}
.containerform header {
  font-size: 35px;
  font-weight: 600;
  margin: 0 0 30px 0;
}
.containerform .form-outer {
  width: 100%;
  overflow: hidden;
}
.containerform .form-outer form {
  display: flex;
  width: calc(100% * var(--stepNumber));
}
.form-outer form .page {
  width: calc(100% / var(--stepNumber));
  transition: margin-left 0.3s ease-in-out;
}
.form-outer form .page .title {
  text-align: left;
  font-size: 25px;
  font-weight: 500;
}
.form-outer form .page .field {
  width: var(--containerWidth);
  height: 45px;
  margin: 45px 0;
  display: flex;
  position: relative;
}
form .page .field .label {
  position: absolute;
  top: -30px;
  font-weight: 500;
}
form .page .field input {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  border: 1px solid black;
  border-radius: 5px;
  padding-left: 15px;
  margin: 0 1px;
  font-size: 18px;
  transition: border-color 150ms ease;
}
form .page .field .leninput {
 margin-left: -10px;
 margin-right: 10px;
}

form .page .field input.invalid-input {
  border-color: red;
}
form .page .field select {
  width: 100%;
  padding-left: 10px;
  font-size: 17px;
  font-weight: 500;
}
form .page .field button {
  width: 120px;
  height: calc(100% + 5px);
  border: none;
  background: #001E6C;
  margin-top: -20px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  font-size: 18px;
  font-weight: 500;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: 0.5s ease;
}
form .page .field button:hover {
  border-radius: 30px;
  background-color: transparent;
  border: 1px solid  #001E6C;
  color: black;
}

form .page .btns button {
  margin-top: 10px;
}
form .page .btns button.prev {
  margin-right: 3px;
  font-size: 17px;
}
form .page .btns button.next {
  margin-left: 3px;

}
.containerform .progress-bar {
  display: flex;
  margin: 40px 0;
  user-select: none;
  flex-direction: row;
  background-color: #001E6C;
  padding-top: 10px;
 
}


.containerform .progress-bar .step {
  text-align: center;
  width: 100%;
  position: relative;
}
.containerform .progress-bar .step p {
  font-weight: 500;
  font-size: 18px;
  color: white;
  margin-bottom: 8px;
}
.progress-bar .step .bullet {
  height: 25px;
  width: 25px;
  border: 2px solid white;
  display: inline-block;
  border-radius: 50%;
  position: relative;
  transition: 0.2s;
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
}
.progress-bar .step .bullet.active {
  border-color: green;
  background: green
}
.progress-bar .step .bullet span {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.progress-bar .step .bullet.active span {
  display: none;
}
.progress-bar .step .bullet:before,
.progress-bar .step .bullet:after {
  position: absolute;
  content: "";
  bottom: 11px;
  right: -51px;
  height: 3px;
  width: 44px;
  
}
.progress-bar .step .bullet.active:after {
  background: var(--primary);
  transform: scaleX(0);
  transform-origin: left;
  animation: animate 0.3s linear forwards;
}


.progress-bar .step:last-child .bullet:before,
.progress-bar .step:last-child .bullet:after {
  display: none;
}
.progress-bar .step p.active {
  color: var(--primary);
  transition: 0.2s linear;
}
.progress-bar .step .check {
  position: absolute;
  left: 50%;
  top: 70%;
  font-size: 15px;
  transform: translate(-50%, -50%);
  display: none;
}

